import request from '@/util/request' ;
import proxy from '../proxy' ;

/**
 *  
 * @param data
 */
export function addTypes(data) {
    return request( {
        url: proxy + '/types/addTypes',
        method: 'POST',
        data
    } )
}

/**
 *  
 * @param data
 */
export function getTypeListPage(data) {
    return request( {
        url: proxy + '/types/getTypeListPage',
        method: 'POST',
        data
    } )
}
